.input {
    background-color: transparent;
    border:  none;
    border-bottom: 1px solid #aaa;
    width: 99%;
    font-family: inherit;
    font-size: inherit;
}
.input.err {
    animation: flasher 0.5s linear; 
}
.input:focus {
    outline: none;
    border-bottom: 1px solid #333;
}
